import Vue from "vue";
import { AppData } from "../ts/app-data";
export default Vue.extend({
    name: "portfolio-sidebar",
    props: {
        currentComponent: {
            required: true,
            type: String
        },
        user: {
            required: true
        }
    },
    methods: {
        signOut: function () {
            AppData.blockstack.signUserOut(window.location.origin);
        },
        changeComponent: function (component) {
            this.$emit("update:current-component", component);
        },
        isSelected: function (component) {
            return this.currentComponent === component;
        },
        showAddModal: function () {
            this.$emit("show-add-modal");
        },
        showSubtractModal: function () {
            this.$emit("show-subtract-modal");
        },
        hasProfileImage: function () {
            return this.user.avatarUrl() != null;
        }
    }
});



