import Vue from "vue";
import PortfolioTotal from "./PortfolioTotal.vue";
import PortfolioBalance from "./PortfolioBalance.vue";
import TimeIntervalPicker from "./../TimeIntervalPicker.vue";
import Spinner from "vue-simple-spinner";
import { TimeInterval } from "../../ts/enums/time-interval";
import { AppData } from "../../ts/app-data";
export default Vue.extend({
    name: "portfolio-overview",
    components: {
        PortfolioBalance: PortfolioBalance,
        PortfolioTotal: PortfolioTotal,
        Spinner: Spinner,
        TimeIntervalPicker: TimeIntervalPicker
    },
    props: {
        reloadData: {
            required: true,
            type: Boolean
        }
    },
    data: function () {
        return {
            balanceData: {},
            errors: [],
            loadedStorage: false,
            primaryColor: AppData.settingsManager.getPrimaryColor(),
            selectedCurrency: "USD",
            selectedCurrencySymbol: "$",
            selectedInterval: TimeInterval.ONE_DAY
        };
    },
    mounted: function () {
        var _this = this;
        AppData.settingsManager.loadSettings().then(function (response) {
            _this.selectedCurrency = AppData.settingsManager.getSelectedCurrency();
            _this.selectedCurrencySymbol = AppData.settingsManager.getSelectedCurrencySymbol();
            _this.selectedInterval = AppData.settingsManager.getTimeInterval();
            _this.loadBalances(false);
        });
    },
    methods: {
        loadBalances: function (ignoreTimer) {
            var _this = this;
            AppData.transactionManager
                .getAllBalances()
                .then(function (balanceData) {
                _this.balanceData = balanceData;
                _this.loadedStorage = true;
                _this.refreshPrices(ignoreTimer);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        refreshPrices: function (ignoreTimer) {
            var _this = this;
            var coins = [];
            for (var key in this.balanceData) {
                coins.push(key);
            }
            // Only check prices if coins have been added
            if (coins.length > 0) {
                AppData.priceManager.refreshPrices(coins, ignoreTimer).then(function (response) {
                    if (response.successful) {
                        // If call was successful, result will be defined
                        var result = response.result;
                        for (var key in result) {
                            // Update data with new prices
                            _this.balanceData[key].price = result[key];
                        }
                    }
                });
            }
        },
        viewTransactionsForCoin: function (coinSymbol) {
            this.$emit("view-transactions", coinSymbol);
        }
    },
    watch: {
        reloadData: function (reload) {
            if (reload) {
                this.$emit("update:reload-data", false);
                this.loadedStorage = false;
                this.loadBalances(true);
            }
        },
        selectedInterval: function (interval) {
            console.log(interval);
        }
    },
    computed: {
        totalBalance: function () {
            var totalBalance = 0;
            if (this.loadedStorage) {
                for (var key in this.balanceData) {
                    // If the coin has a stored price, add it to the total
                    var balance = this.balanceData[key];
                    if (balance.price) {
                        totalBalance += balance.amount * balance.price[this.selectedCurrency];
                    }
                }
            }
            return totalBalance.toFixed(2);
        },
        isEmpty: function () {
            return Object.keys(this.balanceData).length === 0;
        }
    }
});



