            .catch(function (error) {
            console.log(error);
        });
    },
    watch: {
        amount: function (val) {
            var validNumber = this.isValidNumberInput(val);
            if (this.isAddition) {
                this.amountIsValid = validNumber;
            }
            else {
                var amountAvailable = this.maxAmount - val >= 0;
                this.amountIsValid = validNumber && amountAvailable;
            }
        },
        price: function (val) {
            this.priceIsValid = this.isValidNumberInput(val);
        },
        date: function (val) {
            var currentDate = DateTime.local().valueOf();
            var selectedDate = DateTime.fromISO(val).valueOf();
            this.dateIsValid = selectedDate <= currentDate || val === "";
        }
    },
    computed: {
