import Vue from "vue";
import Spinner from "vue-simple-spinner";
export default Vue.extend({
    name: "confirmation-modal",
    components: { Spinner: Spinner },
    props: {
        showLoading: {
            required: false,
            type: Boolean
        }
    },
    data: function () {
        return {
            loading: false
        };
    },
    methods: {
        confirm: function () {
            this.loading = this.showLoading;
            this.$emit("confirm");
        }
    }
});



